:root {
    --container-back: #fff;
    --primary-dark: #f5f5f5;
    --primary: #fff;
    --accent: #3cb4e0;
    --progress-back: #f0f0f0;
    --extra: #a2a3a7;
    --button-background: #fff;
}

.no-mt { margin-top: initial !important; }

.sup-header { font-size: 2rem; font-family: Muli,Roboto,"Helvetica",Arial,sans-serif; font-weight: 600; line-height: 1.6; padding: 20px; }
.sup-header-edit { padding: 0 20px; margin-bottom: 15px; }
.sup-back-link { display: inline-block; width: 35px; color: #0386ba; border: solid 2px; border-radius: 100%; height: 35px; font-weight: bold; font-size: 1.5rem; padding: 4px; vertical-align: sub; position: relative; z-index: 1; }

.overview-container { padding: 30px; width: 100%; }
.overview-container .facitlity-detail { width: 100%; }
.overview-container .img-container { width: 100px; height: 100px; border-radius: 100%; overflow: hidden; }
.overview-container .img-container img { width: 100%; height: 100%; display: block; }
.overview-container .detail-section { vertical-align: top; margin-left: 20px; margin-top: 5px; }
.overview-container .name-header { font-weight: bold; }
.overview-container .secondary-header { font-weight: bold; margin-top: 30px; margin-bottom: 15px; }
.overview-container .third-header {font-weight: bold;margin-top: 10px;margin-bottom: 10px; font-size: 1.5rem; }

.overview-container .form-control { width: calc(100% - 10px); }
.overview-container .form-control.full-width { width: 100%; }
.overview-container .supervision-list-block { margin-top: 20px; width: 100%; counter-reset: sectionCounter;}
.overview-container .section-item { padding: 5px 15px 5px 50px; border: solid 1px #ccc; border-radius: 5px; counter-increment: sectionCounter; position: relative; }
.overview-container .section-item:hover, .overview-container .section-item:focus { border-color: var(--accent); border-width: 2px; outline: none; cursor: pointer; }
.overview-container .section-item + .section-item { margin-top: 10px; }
.overview-container .section-item::before { position: absolute; content: counter(sectionCounter); left: 0; top: 0; font-size: 1.5rem; display: block; width: 50px; height: 50px; text-align: center; line-height: 50px; }
.overview-container .count-span, .overview-container .score-span { color: grey; display: inline-block; margin-bottom: 5px; }
.overview-container .score-span { float: right; }
.overview-container .custom-pos { position: absolute; width: calc(100% - 60px); top: 150px; display: none; }
.overview-container .tab-select { width: 300px; margin-top: 30px; margin-bottom: -20px; max-width: 100%; }

.overview-container .photo-header { font-size: 16px; font-weight: 700; margin-top: 20px; margin-bottom: 20px; color: #303a4b; }
.overview-container .img-section p { font-size: 12px; color: #99b2bf; }
.overview-container .img-section .subheader { margin-top: 10px; margin-bottom: 20px; }
.overview-container .img-section .img-list { display: flex; flex-wrap: wrap; margin-bottom: 20px; gap: 5px; }
.overview-container .img-section .img-container { width: 80px; height: 80px; border-radius: 5px; overflow: hidden; position: relative; }
.overview-container .img-section .img-container img { width: 100%; height: 100%; }
.overview-container .img-section .img-container .remove-img { position: absolute; top: 2px; right: 2px; color: white; background: #0009; width: 25px; padding: 0; min-width: 0; height: 25px; }
.overview-container .img-section .img-container .remove-img svg { width: 15px; }
.overview-container .img-section .img-container .remove-img:hover,
.overview-container .img-section .img-container .remove-img:focus { background: black; }
.overview-container .img-section .img-container .rotate-img { position: absolute; top: 50%; left: 50%; color: white; background: #0009; width: 25px; padding: 0; min-width: 0; transform: translate(-12px, -12px); height: 25px;}
.overview-container .img-section .img-container .rotate-img svg { width: 15px; }
.overview-container .img-section .img-container .rotate-img:hover,
.overview-container .img-section .img-container .rotate-img:focus { background: black; }
.overview-container .img-section .img-select-btn { background: #99b2bf; width: 80px; height: 80px; line-height: 80px; text-align: center; border-radius: 40px; color: white; font-size: 40px; font-weight: normal; }
.overview-container .img-section .img-select-btn:hover, .overview-container .img-section .img-select-btn:focus { opacity: 0.8; }
.overview-container .img-section .img-select-btn:disabled { background: #dbdbdb; }
.overview-container .img-section .img-error { color: red; margin-bottom: 20px; }

.row-container { display: flex; gap: 15px; }
.row-container + .row-container { margin-top: 15px; }

.overview-container .MuiFormControl-root + * { margin-top: 15px; }
.overview-container .form-error { color: red; }

.overview-container label { margin-bottom: 5px; }
.overview-container label .optional { color: grey; }

.overview-container .form-input { padding: 5px; }
.overview-container .MuiSelect-outlined { padding-top: 10.5px; padding-bottom: 10.5px; }
.overview-container .checkbox-control { margin-left: 20px; margin-top: -10px; }

.overview-container .btn-container { margin: 30px 0; width: 100%; text-align: center; }
.overview-container .save-btn { width: 100px; height: 35px; color: white; background: #039be5;}
.overview-container .save-btn:hover { opacity: 0.8; }
.overview-container .fetch-btn { width: 180px; height: 35px; color: white; background: #039be5; margin: 40px 0 20px 0; display: block;}
.overview-container .fetch-btn:hover { opacity: 0.8; }

.group-img-header { text-align: left; width: 100%; color: #424242; margin-bottom: 5px; margin-top: 10px; font-size: 1.4rem; }
.group-img-control { display: flex; }
.group-img-container { position: relative; width: calc(100% - 150px); }

.group-img-container .img-label { margin-bottom: 10px; display: block; width: 100%; font-size: 12px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
.group-img-container .img-btn { display: inline-block; vertical-align: top; margin-top: -5px; }
.group-img-container .img-delete-label { display: inline-block; vertical-align: top; margin-top: -5px; margin-left: 10px; }  
.group-img-container .img-delete-btn { width: 35px; min-width: 0; } 
.group-img-preview { width: 150px; height: auto; max-height: 100px;}
.group-img-preview img { width: 100%; height: 100%; object-fit: contain; } 

.report-p1 { font-size: 14px; color: #1d2327; }
.report-p2 { font-size: 12px; color: #1d2327; padding: 10px; }
.section-p { font-size: 16px; color: #1d2327; padding: 20px 0; }

@media (max-width: 500px) {
    .overview-container .fetch-btn { width: 100%;}
}

.link-btn { width: 100px; height: 35px; color: white; background: #039be5; }

@media (max-width: 420px) {
    .overview-container .count-span, .overview-container .score-span { float: none; display: block; }
}

@media ( max-width: 500px) {
    .overview-container .img-container { display: block; margin: auto; }
    .overview-container .detail-section { text-align: center; margin-left: 0; display: block; }
}

@media (max-width: 768px) {
    .custom-pos { position: initial !important; width: 100% !important; justify-content: start !important; margin-top: 20px; margin-bottom: -20px; }
    .row-container { display: block; }
    .overview-container .MuiFormControl-root + * { margin-top: 15px !important; }
}