:root {
    --container-back: rgb(90, 50, 50);
    --primary-dark: #f5f5f5;
    --primary: #fff;
    --accent: #3cb4e0;
    --progress-back: #f0f0f0;
    --extra: #a2a3a7;
    --button-background: #fff;
}
.no-mt { margin-top: initial !important; }

.iss-header { font-size: 2rem; font-family: Muli,Roboto,"Helvetica",Arial,sans-serif; font-weight: 600; line-height: 1.6; padding: 20px; }
.iss-header-edit { padding: 0 20px; margin-bottom: 15px; }
.iss-back-link { display: inline-block; width: 35px; color: #0386ba; border: solid 2px; border-radius: 100%; height: 35px; font-weight: bold; font-size: 1.5rem; padding: 4px; vertical-align: sub; position: relative; z-index: 1; }

.overview-container { padding: 30px; width: 100%; }
.overview-container .facitlity-detail { width: 100%; }
.overview-container .img-container { width: 100px; height: 100px; border-radius: 100%; overflow: hidden; }
.overview-container .img-container img { width: 100%; height: 100%; display: block; }
.overview-container .detail-section { vertical-align: top; margin-left: 20px; margin-top: 5px; }
.overview-container .name-header { font-weight: bold; }
.overview-container .secondary-header { font-weight: bold; margin-top: 30px; margin-bottom: 15px; }
.overview-container .third-header {font-weight: bold;margin-top: 10px;margin-bottom: 10px; font-size: 1.5rem; }

.overview-container .supervision-list-block { margin-top: 20px; width: 100%; counter-reset: sectionCounter;}
.overview-container .section-item { padding: 5px 15px 5px 50px; border: solid 1px #ccc; border-radius: 5px; counter-increment: sectionCounter; position: relative; }
.overview-container .section-item:hover, .overview-container .section-item:focus { border-color: var(--accent); border-width: 2px; outline: none; cursor: pointer; }
.overview-container .section-item + .section-item { margin-top: 10px; }
.overview-container .section-item::before { position: absolute; content: counter(sectionCounter); left: 0; top: 0; font-size: 1.5rem; display: block; width: 50px; height: 50px; text-align: center; line-height: 50px; }
.overview-container .count-span, .overview-container .score-span { color: grey; display: inline-block; margin-bottom: 5px; }
.overview-container .score-span { float: right; }
.overview-container .custom-pos { position: absolute; width: calc(100% - 60px); top: 150px; display: none; }
.overview-container .tab-select { width: 300px; margin-top: 30px; margin-bottom: -20px; max-width: 100%; }

.row-container { display: flex; gap: 15px; }
.row-container { display: flex; gap: 15px; flex-wrap: wrap; }

.overview-container .MuiFormControl-root + * { margin-top: 15px; }
.overview-container .form-error { color: red; }
.overview-container label { margin-bottom: 5px; }
.overview-container label .optional { color: grey; }
.overview-container .form-input { padding: 5px; }
.overview-container .MuiSelect-outlined { padding-top: 10.5px; padding-bottom: 10.5px; }
.overview-container .btn-container { margin: 30px 0; width: 100%; text-align: center; }
.overview-container .save-btn { width: 100px; height: 35px; color: white; background: #039be5; }
.overview-container .save-btn:hover { opacity: 0.8; }

.link-btn { width: 100px; height: 35px; color: white; background: #039be5; }

.page-container { width: 100%; margin: 0 auto; padding: 30px; }
.page-container .page-block { width: 100%; margin: 0 auto; padding: 30px; background: white; box-shadow: 1px 1px 4px rgba(80,114,125,.4); position: relative; }
.page-container .page-block > div + div { margin-top: 25px; }
.page-header { font-size: 20px; margin: 20px 0; text-align: center; font-family: Helvetica Neue,Helvetica,Arial,sans-serif; font-weight: bold; }
.clear { position: absolute; top: 30px; right: 30px; color: #f14242a1; font-size: 15px; }
.clear:hover { background: #fff0f0; }
.page-container .page-block .back-link { background: transparent; position: absolute; top: 31px; left: 30px; color: #0386ba; }
.page-container .page-block .back-link:hover, .page-container .page-block .back-link:focus { text-decoration: underline; }

.page-container .step-block { margin: 30px 0; }
.step-container { width: 100%; font-family: Helvetica Neue,Helvetica,Arial,sans-serif; position: relative; height: 50px; }
.step-container .step { height: 50px; background: #99b2bf; border-radius: 25px; color: white; line-height: 50px; padding: 0 25px; font-weight: 700; position: absolute; }
.step-container .step:first-child { width: 55%; }
.step-container .step:last-child { width: 55%; right: 0; }
.step-container .active-step { background: #0386ba; }
.step-container .step:not(.active-step):hover, .step-container .step:not(.active-step):focus { text-decoration: underline; cursor: pointer; }

.page-container .form-control { width: calc(50% - 10px); }
.page-container .form-control.full-width { width: 100%; }
.page-container .form-control + .form-control { margin-top: 0px; }
.page-container .form-control label:not([class*="disabled-"]):not([class*="error-"]) { color: #0386ba !important; }
.page-container .form-control .label-correct { top: -3px; left: 15px; }
.page-container .form-control .label-correct[class*="shrink-"] { top: -8px; }
.page-container .form-control fieldset { border-color: #99b2bf; border-width: 1px !important; }
.page-container .form-control fieldset:hover, .page-container .form-control fieldset:focus { border: solid 1px black !important; }
.page-container .form-control fieldset legend span { padding-right: 10px; }
.page-container .form-control p[class*='MuiFormHelperText'] { margin-left: 14px; }
.page-container span[class*='checked-'] { color: #0386ba; }
.page-container .negotiable-control { margin-left: 20px; margin-top: -10px; }
.page-container .form-control.price-control fieldset legend, .page-container .form-control.price-control label { margin-left: 50px; }
.currency {width: 50px; background: #99b2bf5e; height: 50px; display: block; text-align: center; line-height: 50px; font-size: 20px; color: #99b2bf; margin-left: -14px; }
div[class*='MuiAutocomplete-paper'] { border-radius: 5px; }

.next-btn { background: #0386ba; color: white; display: block; margin: 30px auto 0 auto; width: 150px; height: 40px; border-radius: 5px; font-size: 16px; font-weight: normal; }
.next-btn:hover, .page-container .next-btn:focus { opacity: 0.8; }
.term-line { font-weight: 400; font-size: 12px; color: #99b2bf; text-align: center; max-width: 350px; margin: 15px auto; }

@media (max-width: 420px) {
    .overview-container .count-span, .overview-container .score-span { float: none; display: block; }
    .page-container .page-block .clear { position: initial; margin: auto; margin-top: auto; display: block; margin-top: 15px; background: #f4f4f4; border-radius: 5px; width: 150px; }
}
@media ( max-width: 500px) {
    .overview-container .img-container { display: block; margin: auto; }
    .overview-container .detail-section { text-align: center; margin-left: 0; display: block; }
}

@media (max-width: 550px) {
    .step:not(.active-step) .name { display: none; }
    .step:first-child.active-step { width: calc(100% - 80px); z-index: 1; }
    .step:first-child.active-step + .step { text-align: right; }
    .step:last-child.active-step { width: calc(100% - 80px); right: unset; left: 80px; }
}

@media (max-width: 768px) {
    .custom-pos { position: initial !important; width: 100% !important; justify-content: start !important; margin-top: 20px; margin-bottom: -20px; }
    .row-container { display: block; }
    .page-container .form-control { width: 100%; }
    .overview-container .MuiFormControl-root + * { margin-top: 15px !important; }
    .page-container .form-control + .form-control { margin-top: 20px; }
}  