
.progress-container {
    position: relative;
    display: block;
    border-radius: 12px;
    margin: auto;
    height: 12px;
    overflow: hidden;
	background: var(--progress-back);
	margin-bottom: 20px;
}

.progress {
    height: 100%;
    transition: all 0.2s ease;
    background: var(--accent);
}